<template>
	<div id="shop-category">
		<public-header :title="title"></public-header>
		<public-footer :preLoading="showPreLoading" :menufooter="menufooter" v-if="menufooter && menufooter.data"></public-footer>
		<div class="container">
			<diy
				:diydata="diydata"
				:preLoading="showPreLoading"
			>
			</diy>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import PublicFooter from '@/components/footer'
	import diy from '@/components/diy'
	import {mapState, mapMutations} from 'vuex'
	export default {
		data() {
			return {
				showPreLoading: true,
				styleContent: '#fff',
				is_grant: 0,
				title: '会员中心',
				diydata: {
					diy: {
						data: {},
					},
				},
				store: {},
				menufooter: {},
			}
		},
		components: {
			PublicHeader,
			PublicFooter,
			diy,
		},
		computed: {
			...mapState([
				'allcart'
			]),
		},

		methods: {
			...mapMutations([
				'replaceAllcart'
			]),

			onLoad() {
				this.util.request({
					url: 'wmall/mall/mine',
					data: {
						menufooter: 1
					},
				}).then((res) => {
					this.showPreLoading = false;
					this.diydata.goodsLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return;
					}
					this.replaceAllcart(result.message.carts);
					this.diydata.config = result.message.config_mall;
					this.diydata.diy = this.util.extend(this.diydata.diy, result.message.diy);
					this.menufooter = window.menufooter;
				})
			},
		},
		watch: {

		},
		created() {
			this.query = this.util.parseQuery(this.$route.query);
		},
		mounted() {
			var that = this;
			that.onLoad();
		}
	}
</script>

<style>
</style>


